import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';

import menuNavData from '../data/menuNavData';
import HeaderStyles from '../styles/HeaderStyles';
import FullWidthContainer from '../styles/FullWidthContainer';
import SeoulContainer from '../styles/SeoulContainer';

const MenuNavbar = styled.nav`
  z-index: 1;
  left: 0;
  bottom: 0;
  width: 100%;
  /* height: 48px; */
  height: 58px;
  /* background: ${props =>
    props.backgroundFixed ? props.theme.lightOrange : props.theme.lightGrey}; */
    background: ${props => props.theme.lightOrange};
  transition: all 300ms;
  display: flex;
  align-items: center;
  * {
    overflow: hidden;
  }
  .slider-wrapper {
    z-index: 2;
    display: flex;
    justify-content: center;
    height: 48px;
    .slider-inner {
      z-index: 3;
      background: ${props => props.theme.lightGrey};
      position: relative;
      overflow-x: scroll;
      height: 70px;
      width: 600px;
      /* width: 640px; */
      ul.slider {
        z-index: 4;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: 'flex-start';
        flex-wrap: nowrap;
        left: ${props => (props.shouldBeCentered ? '50%' : 0)};
        transform: ${props =>
          props.shouldBeCentered ? 'translateX(-50%)' : 9};
        position: absolute;
        height: 70px;
        /* top: -11px; */
        top: -11px;
        li {
          margin-left: 6px;
          border-radius: 4px;
          height: 40px;
          padding: 2px 0;
          display: flex;
          justify-content: center;
          align-items: center;
          &:last-child {
            margin-right: 6px;
          }
          a {
            width: 100%;
            height: 100%;
            font-family: 'Leckerli One', cursive;
            /* font-family: 'Amatic SC', cursive; */
            font-size: 16px;
            /* font-weight: bold; */
            /* padding: 6px 8px; */
            padding: 0 10px;
            height: 40px;
            display: flex;
            align-items: center;
            border-radius: inherit;
            transition: all 300ms ease-in-out;
            white-space: nowrap;
            color: ${props => props.theme.darkNavy};
             &.menu-nav-active {
              background: ${props => props.theme.darkNavy};
              color: ${props => props.theme.offWhite};
            } 
          }
        }
      }
    }
  }
`;

// const activeClassName = 'menu-nav-active';

// const StyledMenuNavLink = styled(NavLink).attrs({
//   activeClassName: activeClassName
// })`
//   &.${activeClassName} {
//     background: ${props => props.theme.prettyPink};
//     color: ${props => props.theme.offWhite};
//   }
// `;

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fixed: false,
      width: 0
    };
    this.menuNav = React.createRef();
    this.handleMenuNav = this.handleMenuNav.bind(this);
  }
  componentDidMount() {
    this.handleMenuNav();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    }
    if (nextProps.width !== prevState.width) {
      return { width: nextProps.width };
    }
    return false;
  }
  handleMenuNav = () => {
    const { width } = this.props;
    this.setState({ fixed: false, width });
    const menuNavOffsetTop = this.menuNav.current.offsetTop;
    this.props.menuNavbar(menuNavOffsetTop);
  };

  render() {
    const { fixed, width } = this.state;
    // const { width } = this.props;
    const style = {
      width: `${width}px`,
      position: 'absolute'
    };
    const fixedStyle = {
      width: `${width}px`,
      position: 'fixed',
      top: fixed ? this.props.mainNavHeight : '',
      left: 0
    };
    const shouldBeCentered = width < 513 ? false : true;

    return (
      <>
        <HeaderStyles
          backgroundUrl="/images/menu/menu-header-bg2.jpg"
          className="menuHeader"
        >
          <MenuNavbar
            ref={this.menuNav}
            style={fixed ? fixedStyle : style}
            backgroundFixed={fixed}
            width={width}
            // sliderWidth={sliderWidth}
            shouldBeCentered={shouldBeCentered}
            className="menuNav"
          >
            <SeoulContainer className="slider-wrapper">
              <div className="slider-inner">
                <ul className="slider">
                  {menuNavData.map((item, i) => (
                    <li key={item.id}>
                      <NavLink to={item.url} activeClassName="menu-nav-active">
                        {item.name}
                      </NavLink>
                    </li>
                  ))}
                </ul>
              </div>
            </SeoulContainer>
          </MenuNavbar>
        </HeaderStyles>
        <FullWidthContainer
          // backgroundUrl="/images/menu-outer-bg.gif"
          backgroundUrl="/images/contact-header.jpg"
          style={{ height: '20vh' }}
        />
        <div style={{ minHeight: '200vh' }} className="konto" />
        {/* <div>
          {Array.apply(null, Array(30)).map((item, i) => (
            <h1 key={i}>Test</h1>
          ))}
        </div> */}
      </>
    );
  }
}

export default Menu;
