import styled from 'styled-components';

const FullWidthContainer = styled.div`
  width: 100%;
  /* background-image: ${props =>
    props.backgroundUrl ? `url('${props.backgroundUrl}') repeat` : `none`}; */
  ${props =>
    props.backgroundUrl &&
    `background-image: url('${props.backgroundUrl}') repeat`}

`;

export default FullWidthContainer;
